@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap);
*{margin:0;padding:0}body,#root{position:relative;width:100vw;min-height:100vh;overflow-y:auto;overflow-x:hidden;background:#fafafa;font-family:"Poppins", sans-serif}body div.cover,#root div.cover{display:block;height:100vh;display:flex;flex-direction:column;justify-content:center;align-items:center;background:linear-gradient(107.56deg, #0b469d 0%, #ba4ecc 100%)}body div.cover p.scroll-tip,#root div.cover p.scroll-tip{text-align:center;font-weight:800;font-size:18px;align-self:flex-end}body div.splash,#root div.splash{display:flex;flex-direction:column;justify-content:center;align-items:center;color:white}body div.splash h2,#root div.splash h2{font-size:46px}body div.splash h1,#root div.splash h1{text-shadow:0px 4px 4px rgba(0,0,0,0.5);font-size:80px;position:relative;top:-30px}body div.splash>div,#root div.splash>div{width:-webkit-max-content;width:-moz-max-content;width:max-content}body div.pages,#root div.pages{flex:1 1;display:flex;flex-direction:column;align-items:center;overflow-y:auto;flex-wrap:nowrap}body>div.content,#root>div.content{display:flex;flex-direction:row;align-items:center;justify-content:center}body>div.content>div,#root>div.content>div{max-width:925px;width:925px;padding:60px 40px}body>div.content section,#root>div.content section{margin-bottom:40px}body>div.content section>h1,#root>div.content section>h1{font-size:35px;margin-bottom:10px}body>div.content section>p,body>div.content section ul,#root>div.content section>p,#root>div.content section ul{font-size:20px;font-weight:400}body>div.content section ul,#root>div.content section ul{margin-left:25px}body>div.content section a,#root>div.content section a{color:#ba4ecc;text-decoration:none;font-weight:800}body>div.content section a:hover,#root>div.content section a:hover{text-decoration:underline}body>div.content section>div.projects,#root>div.content section>div.projects{width:100%;display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-around}@media screen and (max-width: 675px){body div.splash h2,#root div.splash h2{font-size:25px}body div.splash h1,#root div.splash h1{font-size:45px;top:-10px;margin-bottom:20px}body div.splash>div,#root div.splash>div{width:-webkit-max-content;width:-moz-max-content;width:max-content}}

div.social-links{display:flex;justify-content:space-evenly;position:relative;top:-30px}div.social-links div.social-link{width:50px;height:50px;border-radius:50%;box-shadow:0px 4px 4px rgba(0,0,0,0.25)}div.social-links div.social-link>div{mix-blend-mode:screen;background:white;width:100%;height:100%;border-radius:50%;display:flex;flex-direction:column;justify-content:center;align-items:center}div.social-links div.social-link svg{color:black !important}

div.project{flex-basis:30%;min-width:275px;height:450px;border-radius:10px;margin-bottom:25px;box-shadow:0px 2px 4px rgba(0,0,0,0.25)}div.project header{height:150px;width:100%;background-size:cover;border-top-left-radius:10px;border-top-right-radius:10px;background-image:linear-gradient(107.56deg, #0b469d 0%, #ba4ecc 100%)}div.project header>div{height:100%;width:calc(100% - 20px);padding:0 10px;color:white;display:flex;flex-direction:column;justify-content:center;align-items:center;border-top-left-radius:10px;border-top-right-radius:10px;background:rgba(0,0,0,0.4);text-align:center}div.project header>div h1{font-size:35px}div.project div.content{height:calc(450px - 150px - 20px - 60px);position:relative;background:white;display:flex;flex-direction:column;justify-content:center;align-items:center;padding:10px 40px}div.project div.content>div p{font-size:16px}div.project>div.links{display:flex;flex-direction:row;justify-content:space-evenly;width:60%;padding:0 20%;height:60px;background:white;border-bottom-left-radius:10px;border-bottom-right-radius:10px}div.project>div.links div.link{width:40px;height:40px;background:linear-gradient(107.56deg, #0b469d 0%, #ba4ecc 100%);display:flex;flex-direction:column;justify-content:center;align-items:center;border-radius:50%}div.project>div.links div.link svg{color:white}

